import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CongratuateComponent } from './congratuate/congratuate.component';
import { PrizeWordsComponent } from './prize-words/prize-words.component';

const routes: Routes = [
    { path: '', component: CongratuateComponent },
    { path: 'prize-words', component: PrizeWordsComponent },
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      routes, {
      // NOTICE: If you use `reuse-tab` component and turn on keepingScroll you can set to `disabled`
      // Pls refer to https://ng-alain.com/components/reuse-tab
      scrollPositionRestoration: 'top',
    }
    )],
  exports: [RouterModule],
})
export class RouteRoutingModule { }
