import { Component, OnInit } from '@angular/core';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-prize-words',
  templateUrl: './prize-words.component.html',
  styleUrls: ['./prize-words.component.css']
})
export class PrizeWordsComponent implements OnInit {

  switchValue = false;
  radioValue = null;
  myAuto = null;
  isVisible = false;
  res = null;

  constructor() { }

  ngOnInit(): void {
    this.myAuto = document.getElementById('audio') as HTMLVideoElement;
  }

  ngRadioModelChange($event): void{
    this.isVisible = true;
    if($event === 'Wildasses'){
      this.res = 'right';
    }else{
      this.res = 'wrong';
    }
  }

  ngModelChange($event): void{
    if(this.switchValue){
      this.myAuto.play();
    }else{
      this.myAuto.pause();
    }
  }

  handleOk(): void{

  }

}
